.d-flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}

.justify-around {
  justify-content: space-around;
}

.justify-between {
  justify-content: space-between;
}

.justify-start {
  justify-content: flex-start;
}

.justify-end {
  justify-content: flex-end;
}

.align-center {
  align-items: center;
}

.align-start {
  align-items: flex-start;
}

.align-end {
  align-items: flex-end;
}

.gap-0 {
  gap: 0.1rem;
}

/* Height */
.height-100 {
  height: 100%;
}

.height-50 {
  height: 50%;
}

.height-33 {
  height: 33.3333333333%;
}

/* Width */
.width-100 {
  width: 100%;
}

.width-50 {
  width: 50%;
}

.width-33 {
  width: 33.3333333333%;
}

