.padd-1 {
  padding: 1rem;
}

/* overflow */
.overflow-hidden {
  overflow: hidden;
}

/* Position */
.p-absolute {
  position: absolute;
}

.p-relative {
  position: relative;
}

.middle {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Display */
.d-none {
  display: none;
}

.d-block {
  display: block;
}

/* Border */
.border-round {
  border-radius: 50%;
}

/* Font/Text */
.text-center {
  text-align: center;
}

/* Shadow */
.shadow {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

/* Opacity */
.opacity-50 {
  opacity: 0.5;
}

/* z-index */
.z-index-1 {
  z-index: 1;
}

/* margin */
.margin-auto {
  margin: auto;
